<template>
  <div class="tk-item-list card">
    <div class="card-header" v-if="header !== ''">
      <h5 class="h5 mb-0 text-center">{{ header }}</h5>
    </div>
    <ul>
      <li
        v-for="(item, index) in data"
        :key="index"
        @click.prevent="select(item)"
      >
        <span
          :class="{
            'tk-font-color-black1': value !== item.value,
            'tk-font-color-main': value === item.value,
          }"
          >{{ item.label }}</span
        >
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Array,
      default() {
        return [];
      },
    },
    value: {
      type: Number || String,
      default: 0,
    },
    header: {
      type: String,
      default: "",
    },
  },
  methods: {
    select(item) {
      this.$emit("select", item);
    },
  },
};
</script>

<style scoped>
ul {
  margin-left: 0;
  padding: 0;
}
li {
  list-style: none;
  box-sizing: border-box;
  padding: 8px 0;
  border-bottom: 1px solid #e9ecef;
}
li:hover {
  background-color: #f6f9fc;
}
