  width: 160px;
  height: auto;
  max-height: 160px;
  background-color: white;
  overflow-x: hidden;
  overflow-y: auto;
  text-align: left;
  box-sizing: border-box;
  padding: 0 1rem;
}
.tk-item-list::-webkit-scrollbar {
  display: none;
}
</style>